@import './variables';
@import './flex';

@font-face {
    font-family: verdana;
    src: url('./../assets/Verdana.ttf');
}

html {
    flex: 1;
}

body {
    margin: 0;
    padding: 0;
    font-family: verdana;
    @extend .f-c;
    flex: 1;
}

#root {
    @extend .f-c;
    flex: 1;
}

button {
    border: none;
    outline: none;
}

button.btn-primary {
    background: $dark;
    cursor: pointer;
    padding: 0px 20px;
    height: 35px;
    text-align: center;
    border-radius: 3px;
    color: white;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
    transition: all .2s ease;
    &:hover {
        background-image: -webkit-linear-gradient(90deg, #34495D 0%, #161616 100%);
    }
}

button.btn-full {
    width: 100%;
}